@import url(https://fonts.googleapis.com/css2?family=Itim&display=swap);
.home ul{text-align:left;margin-left:30%}.home ul div{margin-bottom:5px;text-decoration:underline}.home ul li{margin-bottom:5px}.home .projects{display:flex;flex-wrap:wrap;align-items:center;justify-content:center}.home .projects .project{box-shadow:3px 3px 10px black;max-width:500px;min-width:-webkit-min-content;min-width:min-content;cursor:pointer;transition-duration:150ms;transition-property:-webkit-transform;transition-property:transform;transition-property:transform, -webkit-transform}.home .projects .project .link{display:inline-block;font-size:30px;font-weight:bold}.home .projects .project:hover{-webkit-transform:translateY(-2px);transform:translateY(-2px)}.home .links{font-size:22px;font-weight:bold;word-wrap:break-word}.home .links a{font-weight:normal}

.login button{margin-top:7px;margin-bottom:15px}.account .avatar{display:inline-flex}.account .avatar>span{margin:auto 5px}.account .avatar button{margin:auto 10px}.account .avatar .display{position:relative;display:flex;height:100px;width:100px;cursor:pointer}.account .avatar .display img{background-color:#4b4b4b;border-radius:50px;display:flex;align-items:center;transition-property:-webkit-filter;transition-property:filter;transition-property:filter, -webkit-filter;transition-duration:150ms}.account .avatar .display:hover img{-webkit-filter:brightness(50%);filter:brightness(50%)}.account .avatar .display span{position:absolute;display:flex;align-items:center;height:100%;width:100%;opacity:0;transition-property:opacity;transition-duration:150ms}.account .avatar .display:hover span{opacity:1}.account .buttons button{margin:15px 5px}.account div>span{margin-right:5px}

@-webkit-keyframes spin{100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes spin{100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@-webkit-keyframes bounceIn{0%,20%,40%,60%,80%,to{-webkit-animation-timing-function:cubic-bezier(0.215, 0.61, 0.355, 1);animation-timing-function:cubic-bezier(0.215, 0.61, 0.355, 1)}0%{opacity:0;-webkit-transform:scale3d(0.3, 0.3, 0.3);transform:scale3d(0.3, 0.3, 0.3)}20%{-webkit-transform:scale3d(1.1, 1.1, 1.1);transform:scale3d(1.1, 1.1, 1.1)}40%{-webkit-transform:scale3d(0.9, 0.9, 0.9);transform:scale3d(0.9, 0.9, 0.9)}60%{opacity:1;-webkit-transform:scale3d(1.03, 1.03, 1.03);transform:scale3d(1.03, 1.03, 1.03)}80%{-webkit-transform:scale3d(0.97, 0.97, 0.97);transform:scale3d(0.97, 0.97, 0.97)}to{opacity:1;-webkit-transform:scaleX(1);transform:scaleX(1)}}@keyframes bounceIn{0%,20%,40%,60%,80%,to{-webkit-animation-timing-function:cubic-bezier(0.215, 0.61, 0.355, 1);animation-timing-function:cubic-bezier(0.215, 0.61, 0.355, 1)}0%{opacity:0;-webkit-transform:scale3d(0.3, 0.3, 0.3);transform:scale3d(0.3, 0.3, 0.3)}20%{-webkit-transform:scale3d(1.1, 1.1, 1.1);transform:scale3d(1.1, 1.1, 1.1)}40%{-webkit-transform:scale3d(0.9, 0.9, 0.9);transform:scale3d(0.9, 0.9, 0.9)}60%{opacity:1;-webkit-transform:scale3d(1.03, 1.03, 1.03);transform:scale3d(1.03, 1.03, 1.03)}80%{-webkit-transform:scale3d(0.97, 0.97, 0.97);transform:scale3d(0.97, 0.97, 0.97)}to{opacity:1;-webkit-transform:scaleX(1);transform:scaleX(1)}}body{margin:0;font-family:sans-serif;background-color:#323232;color:white;--header-height: 85px;--footer-height: 0}.header{display:flex;background-color:#646464;height:var(--header-height)}.header .logo{display:flex}.header .logo img{padding:5px;height:75px}.header .logo span{margin:auto 10px;color:white;font-size:30px;font-family:Itim}.header .login{margin:auto;margin-right:15px;padding:10px;border-radius:10px;background-color:#323232;font-size:20px;text-decoration:none;color:white;transition-duration:150ms;transition-property:background-color}.header .login:hover{background-color:#4b4b4b}.header .menu{margin:auto;margin-top:calc((85px - 60px) / 2);margin-right:15px;text-align:center;border-radius:10px;background-color:#282828}.header .menu .user{display:flex;padding:10px;border-radius:10px;cursor:pointer;transition-property:background-color;transition-duration:150ms}.header .menu .user:hover{background-color:#4b4b4b}.header .menu .user img{border-radius:20px;height:40px;width:40px;background-color:#4b4b4b}.header .menu .user span{margin:auto;margin-left:10px;font-size:18px}.header .menu a{display:block;border-radius:10px;padding:5px;text-decoration:none;color:white}.header .menu a:hover{background-color:#4b4b4b}.content{text-align:center;padding:30px;font-size:18px;min-height:calc(100vh - (var(--header-height) + var(--footer-height) + 30px * 2))}.content .title{display:block;font-size:42px;font-weight:bold;margin-bottom:10px}.content .subtitle{display:block;font-size:30px;text-decoration:underline;margin-top:10px;margin-bottom:20px}.footer{width:100%;height:var(--footer-height);background-color:#282828}.box{display:inline-block;border-radius:15px;margin:10px;padding:15px;background-color:#646464;font-size:18px;color:white;text-decoration:none}.box a{vertical-align:middle;font-size:30px;font-weight:bold}.box img{vertical-align:middle;height:75px;margin-left:15px;margin-bottom:5px;border-radius:20px}.box .name{vertical-align:middle;font-size:30px;font-weight:bold;padding:5px}.box .subname{padding-top:10px;font-weight:bold}button{padding:8px;background-color:#646464;font-size:18px;text-decoration:none;color:white;cursor:pointer;border-width:0;border-radius:10px;transition-duration:150ms;transition-property:background-color, -webkit-transform;transition-property:background-color, transform;transition-property:background-color, transform, -webkit-transform}button:hover{background-color:#4b4b4b}button:active{-webkit-transform:translateY(3px);transform:translateY(3px)}input[type="text"],input[type="password"]{padding:5px;border-width:0;border-radius:5px;outline:none}input[type="file"]{display:none}input[type="checkbox"]{-webkit-transform:scale(1.4);transform:scale(1.4)}a{text-decoration:none;color:white}a .link{position:relative}a .link::after{position:absolute;content:"";-webkit-transform-origin:0 0;transform-origin:0 0;-webkit-transform:scaleX(0);transform:scaleX(0);bottom:0;left:0;right:0;height:2px;background:currentColor;transition:-webkit-transform 250ms;transition:transform 250ms;transition:transform 250ms, -webkit-transform 250ms}a:hover .link::after{-webkit-transform:scaleX(1);transform:scaleX(1)}.loading i{font-size:38px;-webkit-animation:1s linear infinite spin;animation:1s linear infinite spin;margin-bottom:10px}.info{margin:10px auto;width:-webkit-max-content;width:max-content;padding:10px;font-size:18px;color:white;border-radius:10px;-webkit-animation-duration:0.75s;animation-duration:0.75s;-webkit-animation-name:bounceIn;animation-name:bounceIn}.hint{display:inline-block;margin:7px}

